import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Bonus } from '../Bonus/Bonus';
import './OtherBonuses.css'

interface BonusItem {
  img: string;
  name: string;
  balance: string;
}

interface OtherBonusesProps {
  bonusList: BonusItem[];
}


export default function OtherBonuses({bonusList}: OtherBonusesProps) {
  const [expanded, setExpanded] = React.useState<string | false>(false);

  const handleChange =
    (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
      setExpanded(isExpanded ? panel : false);
    };

  return (
    <div>
      <Accordion expanded={expanded === 'close'} onChange={handleChange('close')} style={{ margin: 0, padding: 0 }}>
        <AccordionSummary  
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <div className='OtherBonusesWrapper' >
            <div className='OtherBonusesImages' style={{width: expanded == false ? '60px' : '0'}}>
              <div className='OtherBonusesImage OtherBonusesImage1'>
                <img src={bonusList[0].img} alt="" style={{display: expanded == false ? 'block' : 'none'}}/>
              </div>
              <div className='OtherBonusesImage OtherBonusesImage2'>
                <img src={bonusList[1].img} alt="" style={{display: expanded == false ? 'block' : 'none'}}/>
              </div>
              
            </div>
          <Typography style={{marginLeft: expanded == false ? 0 : 60}}>Другие активы</Typography>
          </div>
        </AccordionSummary>
        <AccordionDetails>
          {
            bonusList.map((item) => (
              <Bonus img={item.img} name={item.name} balance={item.balance}/>
            ))
          }
        </AccordionDetails>
      </Accordion>
    </div>
  );
}